
import XButton from 'vux/src/components/x-button/index.vue'

export default {
props: ['result', 'from', 'index', 'type'],
data () {
return {
loading1: false,
loading2: false
}
},
components: {
XButton
},
methods: {
addManager (id) {

this.$emit('on-add', {
id: id,
index: this.index,
cb: () => {

}
})
},
deleteManager (id) {
this.$emit('on-delete', {
id: id,
index: this.index,
cb: () => {
this.loading2 = false
}
})
},
addBlack (id) {
this.$emit('on-black', {
id: id,
index: this.index,
cb: () => {

}
})
},
releaseBlack (id) {
this.$emit('on-release', {
id: id,
index: this.index,
cb: () => {

}
})
},
onRoute (id) {
if (this.type === 1) {
return false
}
this.$emit('on-item-click', {
id: id,
index: this.index
})
}
}
}



